<style>
  .m-carManage-carApply{
    padding: 20px;
  }
  .m-carManage-carApply-header{
    margin-bottom: 20px;
  }
  .m-carManage-carApply-dialog .detail-title {
    font-size: 15px;
    border-bottom: 1px #ddd solid;
    margin: 20px 0;
    font-weight: bold;
  }
</style>
<template>
  <div class="m-carManage-carApply">
    <el-table
      v-loading="loading"
      :data="rows_inShow"
      stripe
      style="width: 100%">
      <el-table-column
        v-for="col in cols"
        :key="col.prop"
        :prop="col.prop"
        :label="col.label"
        :width="col.width">
      </el-table-column>
      <el-table-column
        label="操作"
        width="200">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="onCheckEvent(scope.$index)"
            type="text">
            详情
          </el-button>
          <el-button
            v-if="scope.row.status !== '审核通过'"
            @click.native.prevent="onAuditEvent(scope.$index,1)"
            type="text">
            通过
          </el-button>
          <el-button
            v-if="scope.row.status !== '审核驳回'"
            @click.native.prevent="onAuditEvent(scope.$index,-1)"
            type="text">
            驳回
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog class="m-carManage-carApply-dialog" append-to-body title="车辆申请详情" :visible.sync="detailDialog">
      <div class="detail-title">申请信息</div>
      <el-row>
        <el-col :span="12">开始时间：{{ item.apply_info.start }}</el-col>
        <el-col :span="12">结束时间：{{ item.apply_info.end }}</el-col>
      </el-row>
      <el-row>
        <el-col :span="12">申请用途：{{ item.apply_info.use }}</el-col>
        <el-col :span="12">审核状态：{{ item.apply_info.status | statusFilter }}</el-col>
      </el-row>
      <el-row>
        <el-col :span="24">审核意见：{{ item.apply_info.opinion }}</el-col>
      </el-row>
      <div class="detail-title">车辆信息</div>
      <el-row>
        <el-col :span="12">车牌号码：{{ item.car.car_license }}</el-col>
        <el-col :span="12">车辆品牌：{{ item.car.brand }}</el-col>
      </el-row>
      <el-row>
        <el-col :span="12">型号：{{ item.car.model }}</el-col>
        <el-col :span="12">颜色：{{ item.car.color }}</el-col>
      </el-row>
      <el-row>
        <el-col :span="12">车架号：{{ item.car.frame_number }}</el-col>
        <el-col :span="12">发动机号：{{ item.car.engine_number }}</el-col>
      </el-row>
      <el-row>
        <el-col :span="12">车辆状态：{{ item.car.car_status }}</el-col>
        <el-col :span="12">借用情况：{{ item.car.borrow_status }}</el-col>
      </el-row>
      <div class="detail-title">申请人信息</div>
      <el-row>
        <el-col :span="12">名字：{{ item.car.username }}</el-col>
      </el-row>
      <el-row>
        <el-col :span="12">所属公司：{{ item.user_info.company }}</el-col>
        <el-col :span="12">所属分组：{{ item.user_info.group }}</el-col>
      </el-row>
    </el-dialog>
  </div>
</template>
<script>
import { rest } from '@/common/request';

export default {
  data(){
    return {
      cols:[
        {
          prop: 'id',
          label: 'ID'
        },{
          prop: 'user_name',
          label: '用户'
        },{
          prop: 'company',
          label: '公司'
        },{
          prop: 'car_lincese',
          label: '车牌号码'
        },{
          prop: 'start',
          label: '开始时间'
        },{
          prop: 'end',
          label: '结束时间'
        },{
          prop: 'status',
          label: '申请状态'
        }
      ],
      rows: [],
      item: {
        apply_info:{},
        car:{},
        user_info:{}
      },
      rules: {
        company_name:[
          {required: true, message: '请输入公司名称', trigger: 'blur'}
        ]
      },
      loading: true,
      detailDialog: false,
      isEdit: false
    }
  },
  filters:{
    statusFilter(val){
      switch(val){
        case 0: return '待审核';
        case 1: return '审核通过';
        case -1: return '审核驳回';
      }
    }
  },
  computed:{
    rows_inShow(){
      return this.rows.map(item => {
        if(item.status === 0){
          item.status = '待审核';
        }else if(item.status === 1){
          item.status = '审核通过';
        }else{
          item.status = '审核驳回';
        }
        return item;
      })
    }
  },
  methods:{
    fetchList(){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/car_apply/list',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'GET'
      }).then(res => {
        this.rows = res.data.data;
        this.loading = false;
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      });
    },
    getItem(id){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/car_apply/detail',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        params:{
          apply_id: id
        }
      }).then(res => {
        this.item = res.data.data;
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      });
    },
    passItem(id){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/car_apply/handle',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data:{
          id,
          status: 1,
          opinion: "通过"
        }
      }).then(res => {
        this.$message({
          message: '车辆审核通过！',
          type: 'success'
        });
        this.fetchList();
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      });
    },
    rejectItem(id, opinion){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/car_apply/handle',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data:{
          id,
          status: -1,
          opinion
        }
      }).then(res => {
        this.$message({
          message: '车辆申请已驳回！',
          type: 'success'
        });
        this.fetchList();
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      });
    },
    onAuditEvent(index, status){
      if(status === 1){
        this.$confirm(`是否通过id为${this.rows[index].id}的车辆申请？`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.passItem(this.rows[index].id);
        })
      }else{
        this.$prompt(`驳回该车辆申请（id:${this.rows[index].id}）的理由`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputType: 'textarea',
          inputPattern: /\S/,
          inputErrorMessage: '请输入驳回理由'
        }).then(({ value })=>{
          this.rejectItem(this.rows[index].id, value);
        })
      }
      
    },
    onCheckEvent(index){
      this.getItem(this.rows[index].id);
      this.detailDialog = true;
    },
  },
  mounted(){
    this.fetchList();
  }
}
</script>




